.toggle {
  display: inline-block;
  z-index: 999;
  position: absolute;
  right: 15px;
  top: 15px;
}

.toggle__label {
  position: relative;
  background-color: $black-color;
  display: flex;
  justify-content: space-between;
  border-radius: 50px;
  padding: 5px;
  height: 29px;
  width: 58px;
  cursor: pointer;
  &:focus .toggle__ball,
  &:hover .toggle__ball {
    box-shadow: 0 0 4px 4px $accent-color;
    transition: $time;
  }
}

.toggle__checkbox {
  opacity: 0;
  position: absolute;
}

.toggle__checkbox:checked + .toggle__label .toggle__ball {
  transform: translateX(29px);
}

.toggle__ball {
  background-color: $grey-color;
  border-radius: 50%;
  position: absolute;
  top: 2px;
  left: 2px;
  height: 25px;
  width: 25px;
}

body.light-theme {
  background-color: $main-color;
  color: $black-color;
}

body.dark-theme {
  background-color: $dark-theme-color;
  color: $white-color;
}
.section--footer.light-theme {
  background-color: $footer-bg-color;
  color: $black-color;
  transition: $time;
}
.section--footer.dark-theme {
  background-color: $dark-theme-color;
  color: $white-color;
  transition: $time;
  & .footer__text {
    color: $white-color;
    transition: $time;
  }
}

